"use client"

import Link from "next/link"
import { useState, useEffect, useRef, useCallback } from "react"
import { Menu, X, Facebook, Twitter, Instagram, Linkedin, Youtube, ChevronDown, ChevronRight } from "lucide-react"
import Image from "next/image"
import { collection, query, orderBy, getDocs } from "firebase/firestore"
import { db } from "@/lib/firebase"

interface MenuItem {
  id: string
  title: string
  type: "main" | "submenu"
  parentId: string | null
  order: number
  level: number
  image?: string
  children?: MenuItem[]
}

export function Navigation() {
  const [isOpen, setIsOpen] = useState(false)
  const [teamMenuOpen, setTeamMenuOpen] = useState(false)
  const [menuHierarchy, setMenuHierarchy] = useState<MenuItem[]>([])
  const [loading, setLoading] = useState(true)
  const [expandedMobileMenus, setExpandedMobileMenus] = useState<Set<string>>(new Set())
  const [activeDesktopMenu, setActiveDesktopMenu] = useState<string | null>(null)
  const [isDesktop, setIsDesktop] = useState(false)
  const [touchStart, setTouchStart] = useState<number | null>(null)
  
  const teamMenuRef = useRef<HTMLDivElement>(null)
  const desktopMenuTimeoutRef = useRef<NodeJS.Timeout | null>(null)
  const mobileMenuRef = useRef<HTMLDivElement>(null)
  const navRef = useRef<HTMLDivElement>(null)

  // Check if device is desktop
  useEffect(() => {
    const checkIfDesktop = () => {
      setIsDesktop(window.innerWidth >= 1024)
    }
    
    checkIfDesktop()
    window.addEventListener('resize', checkIfDesktop)
    
    return () => {
      window.removeEventListener('resize', checkIfDesktop)
    }
  }, [])

  // Handle click outside to close menus
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (teamMenuRef.current && !teamMenuRef.current.contains(event.target as Node)) {
        setTeamMenuOpen(false)
        setActiveDesktopMenu(null)
      }
    }

    document.addEventListener('mousedown', handleClickOutside)
    return () => document.removeEventListener('mousedown', handleClickOutside)
  }, [])

  // Handle escape key
  useEffect(() => {
    const handleEscape = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        if (teamMenuOpen) {
          setTeamMenuOpen(false)
          setActiveDesktopMenu(null)
        }
        if (isOpen) {
          setIsOpen(false)
          setExpandedMobileMenus(new Set())
        }
      }
    }

    document.addEventListener('keydown', handleEscape)
    return () => document.removeEventListener('keydown', handleEscape)
  }, [teamMenuOpen, isOpen])

  // Load menu data
  useEffect(() => {
    const loadData = async () => {
      try {
        const menuQuery = query(collection(db, "menuItems"), orderBy("order"))
        const menuSnapshot = await getDocs(menuQuery)
        
        const allItems: MenuItem[] = []
        menuSnapshot.forEach((doc) => {
          const data = doc.data()
          allItems.push({
            id: doc.id,
            title: data.title || "",
            type: data.type || "main",
            parentId: data.parentId || null,
            order: data.order || 0,
            level: data.level || 0,
            image: data.image || "",
            children: []
          })
        })
        
        const hierarchy = buildMenuHierarchy(allItems)
        setMenuHierarchy(hierarchy)
      } catch (error) {
        console.error("Error loading navigation data:", error)
      } finally {
        setLoading(false)
      }
    }

    loadData()
  }, [])

  // Build menu hierarchy
  const buildMenuHierarchy = (items: MenuItem[]): MenuItem[] => {
    const itemMap: { [key: string]: MenuItem } = {}
    const tree: MenuItem[] = []
    
    items.forEach(item => {
      itemMap[item.id] = { ...item, children: [] }
    })
    
    items.forEach(item => {
      const currentItem = itemMap[item.id]
      if (item.parentId && itemMap[item.parentId]) {
        if (!itemMap[item.parentId].children) {
          itemMap[item.parentId].children = []
        }
        itemMap[item.parentId].children!.push(currentItem)
      } else if (item.type === "main") {
        tree.push(currentItem)
      }
    })
    
    const sortChildren = (menuList: MenuItem[]) => {
      menuList.sort((a, b) => a.order - b.order)
      menuList.forEach(menu => {
        if (menu.children && menu.children.length > 0) {
          sortChildren(menu.children)
        }
      })
    }
    
    sortChildren(tree)
    return tree
  }

  // Desktop menu handlers
  const handleDesktopMenuEnter = useCallback((menuId: string) => {
    if (desktopMenuTimeoutRef.current) {
      clearTimeout(desktopMenuTimeoutRef.current)
    }
    setActiveDesktopMenu(menuId)
    setTeamMenuOpen(true)
  }, [])

  const handleDesktopMenuLeave = useCallback(() => {
    desktopMenuTimeoutRef.current = setTimeout(() => {
      setTeamMenuOpen(false)
      setActiveDesktopMenu(null)
    }, 200)
  }, [])

  const handleDesktopMenuClick = useCallback((menuId: string) => {
    if (activeDesktopMenu === menuId) {
      setTeamMenuOpen(false)
      setActiveDesktopMenu(null)
    } else {
      setTeamMenuOpen(true)
      setActiveDesktopMenu(menuId)
    }
  }, [activeDesktopMenu])

  // Mobile menu handlers with touch support
  const toggleMobileMenu = (menuId: string) => {
    const newExpanded = new Set(expandedMobileMenus)
    if (newExpanded.has(menuId)) {
      newExpanded.delete(menuId)
    } else {
      newExpanded.add(menuId)
    }
    setExpandedMobileMenus(newExpanded)
  }

  const handleTouchStart = (e: React.TouchEvent) => {
    setTouchStart(e.touches[0].clientX)
  }

  const handleTouchEnd = (e: React.TouchEvent) => {
    if (touchStart === null) return
    
    const touchEnd = e.changedTouches[0].clientX
    const diff = touchStart - touchEnd
    
    // Swipe left to close menu
    if (diff > 50 && isOpen) {
      setIsOpen(false)
      setExpandedMobileMenus(new Set())
    }
    
    setTouchStart(null)
  }

  // Close all menus
  const closeAllMenus = () => {
    setIsOpen(false)
    setTeamMenuOpen(false)
    setActiveDesktopMenu(null)
    setExpandedMobileMenus(new Set())
  }

  // Render desktop team menu
  const renderDesktopTeamMenu = (menus: MenuItem[], level = 0) => {
    if (loading) {
      return (
        <div className="px-4 py-8 flex items-center justify-center">
          <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600"></div>
        </div>
      )
    }

    if (menus.length === 0) {
      return (
        <div className="px-4 py-3 text-sm text-gray-500">
          No team sections configured.
        </div>
      )
    }

    return menus.map((menu) => {
      const isActive = activeDesktopMenu === menu.id
      const hasChildren = menu.children && menu.children.length > 0
      
      return (
        <div 
          key={menu.id}
          className="group"
          onMouseEnter={() => hasChildren && handleDesktopMenuEnter(menu.id)}
          onMouseLeave={() => hasChildren && handleDesktopMenuLeave()}
        >
          <div className={`flex items-center justify-between rounded-lg px-3 py-2 transition-all duration-200 ${
            isActive ? 'bg-blue-50 text-blue-700' : 'hover:bg-gray-50'
          }`}>
            <Link
              href={`/team/${menu.id}`}
              className={`flex-1 font-medium transition-colors ${
                level > 0 ? 'text-sm' : ''
              } ${isActive ? 'text-blue-700' : 'text-gray-700 hover:text-black'}`}
              onClick={closeAllMenus}
              style={{ paddingLeft: `${level * 12}px` }}
            >
              {menu.title}
            </Link>
            
            {hasChildren && (
              <button
                onClick={(e) => {
                  e.preventDefault()
                  e.stopPropagation()
                  handleDesktopMenuClick(menu.id)
                }}
                className={`p-1 rounded transition-colors ${
                  isActive ? 'text-blue-600' : 'text-gray-400 hover:text-gray-600'
                }`}
                aria-label={isActive ? `Collapse ${menu.title}` : `Expand ${menu.title}`}
              >
                <ChevronRight 
                  className={`w-4 h-4 transition-transform duration-200 ${
                    isActive ? 'rotate-90' : ''
                  }`}
                />
              </button>
            )}
          </div>
          
          {/* Children with smooth transition */}
          {isActive && hasChildren && menu.children && (
            <div 
              className="mt-1 ml-2 pl-2 border-l border-gray-200"
              style={{
                animation: 'slideDown 0.3s ease-out'
              }}
            >
              {renderDesktopTeamMenu(menu.children, level + 1)}
            </div>
          )}
        </div>
      )
    })
  }

  // Render mobile team menu
  const renderMobileTeamMenu = (menus: MenuItem[], level = 0) => {
    return menus.map((menu) => {
      const isExpanded = expandedMobileMenus.has(menu.id)
      const hasChildren = menu.children && menu.children.length > 0

      return (
        <div 
          key={menu.id} 
          className={`${level > 0 ? 'ml-4 pl-4 border-l border-gray-200' : ''}`}
        >
          <div className="flex items-center justify-between py-3 rounded-lg px-3 hover:bg-gray-50 transition-colors">
            <Link
              href={`/team/${menu.id}`}
              className={`flex-1 text-gray-700 hover:text-black ${
                level > 0 ? 'text-sm' : 'font-medium'
              }`}
              onClick={() => {
                if (!hasChildren) {
                  closeAllMenus()
                }
              }}
            >
              {menu.title}
            </Link>
            
            {hasChildren && (
              <button
                onClick={(e) => {
                  e.preventDefault()
                  e.stopPropagation()
                  toggleMobileMenu(menu.id)
                }}
                className="p-1 hover:bg-gray-200 rounded transition-colors"
                aria-label={isExpanded ? `Collapse ${menu.title}` : `Expand ${menu.title}`}
              >
                <ChevronRight 
                  className={`w-4 h-4 transition-transform duration-200 ${
                    isExpanded ? 'rotate-90' : ''
                  }`}
                />
              </button>
            )}
          </div>
          
          {/* Children with animation */}
          {isExpanded && hasChildren && menu.children && (
            <div 
              className="overflow-hidden"
              style={{
                animation: 'slideDown 0.3s ease-out'
              }}
            >
              {renderMobileTeamMenu(menu.children, level + 1)}
            </div>
          )}
        </div>
      )
    })
  }

  const links = [
    { href: "/", label: "Home" },
    { href: "/about", label: "About" },
    { href: "/our-work", label: "Our Work" },
    { href: "/contact", label: "Contact Us" },
  ]

  const socialLinks = [
    { 
      href: "https://www.facebook.com/share/1SpxhPXM8r/", 
      label: "Facebook", 
      icon: <Facebook className="w-4 h-4" />,
      ariaLabel: "Visit our Facebook page"
    },
    { 
      href: "https://www.instagram.com/aapkasahyogfoundation?igsh=ZzRhbjZ6dWVkYm1p", 
      label: "Instagram", 
      icon: <Instagram className="w-4 h-4" />,
      ariaLabel: "Visit our Instagram page"
    },
    { 
      href: "https://youtube.com/@aapkasahyogfoundation?si=grYCLhqeIoBHVOSc", 
      label: "YouTube", 
      icon: <Youtube className="w-4 h-4" />,
      ariaLabel: "Visit our YouTube channel"
    },
  ]

  // Add CSS for animations
  useEffect(() => {
    const style = document.createElement('style')
    style.textContent = `
      @keyframes slideDown {
        from {
          opacity: 0;
          transform: translateY(-10px);
          max-height: 0;
        }
        to {
          opacity: 1;
          transform: translateY(0);
          max-height: 500px;
        }
      }
      
      @keyframes slideUp {
        from {
          opacity: 0;
          transform: translateY(20px);
        }
        to {
          opacity: 1;
          transform: translateY(0);
        }
      }
      
      @keyframes fadeIn {
        from {
          opacity: 0;
        }
        to {
          opacity: 1;
        }
      }
      
      .nav-slide-down {
        animation: slideDown 0.3s ease-out;
      }
      
      .nav-slide-up {
        animation: slideUp 0.3s ease-out;
      }
      
      .nav-fade-in {
        animation: fadeIn 0.2s ease-out;
      }
    `
    document.head.appendChild(style)
    
    return () => {
      document.head.removeChild(style)
    }
  }, [])

  return (
    <>
      <nav 
        ref={navRef}
        className="sticky top-0 z-50 w-full bg-white/95 backdrop-blur-sm border-b border-gray-200 shadow-sm"
        onTouchStart={handleTouchStart}
        onTouchEnd={handleTouchEnd}
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16 lg:h-20">
            {/* Logo */}
            <Link 
              href="/" 
              className="flex items-center gap-3 group"
              onClick={closeAllMenus}
            >
              <div className="relative w-10 h-10 lg:w-12 lg:h-12 bg-gradient-to-br from-blue-600 to-blue-800 rounded-full flex items-center justify-center overflow-hidden shadow-md group-hover:shadow-lg transition-shadow duration-300">
                <Image 
                  src="/images/asf-logo.png" 
                  alt="ASF Logo" 
                  width={48}
                  height={48}
                  className="w-8 h-8 lg:w-10 lg:h-10 object-contain"
                  priority
                />
              </div>
              <div className="flex flex-col">
                <span className="font-bold text-gray-900 text-lg lg:text-xl leading-tight">
                  Aapka Sahyog
                </span>
                <span className="text-xs lg:text-sm text-gray-600">
                  Foundation
                </span>
              </div>
            </Link>

            {/* Desktop Navigation */}
            <div className="hidden lg:flex gap-6 xl:gap-8 items-center">
              {links.map((link, index) => (
                <Link 
                  key={link.href} 
                  href={link.href}
                  className="relative text-sm font-medium text-gray-700 hover:text-blue-600 transition-colors duration-200 after:absolute after:bottom-0 after:left-0 after:w-0 after:h-0.5 after:bg-blue-600 after:transition-all after:duration-300 hover:after:w-full"
                  style={{ animationDelay: `${index * 50}ms` }}
                  onClick={closeAllMenus}
                >
                  {link.label}
                </Link>
              ))}
              
              {/* Team Dropdown */}
              <div 
                ref={teamMenuRef}
                className="relative"
                onMouseEnter={() => handleDesktopMenuEnter('team')}
                onMouseLeave={handleDesktopMenuLeave}
              >
                <button
                  onClick={() => handleDesktopMenuClick('team')}
                  className={`flex items-center gap-1 text-sm font-medium transition-colors duration-200 ${
                    teamMenuOpen 
                      ? 'text-blue-600' 
                      : 'text-gray-700 hover:text-blue-600'
                  }`}
                  aria-expanded={teamMenuOpen}
                >
                  Team
                  <ChevronDown className={`w-4 h-4 transition-transform duration-200 ${
                    teamMenuOpen ? 'rotate-180' : ''
                  }`} />
                </button>
                
                {/* Team Menu Dropdown */}
                {teamMenuOpen && (
                  <div 
                    className="absolute left-0 top-full mt-2 w-72 bg-white rounded-xl shadow-2xl border border-gray-200 overflow-hidden nav-slide-down z-50"
                  >
                    <div className="p-2">
                      <div className="flex items-center justify-between px-3 py-2 border-b border-gray-100 mb-2">
                        <h3 className="font-semibold text-gray-900">Team Structure</h3>
                        <span className="text-xs text-gray-500 bg-gray-100 px-2 py-1 rounded-full">
                          {menuHierarchy.length} sections
                        </span>
                      </div>
                      
                      <div className="max-h-[60vh] overflow-y-auto pr-1">
                        {renderDesktopTeamMenu(menuHierarchy)}
                      </div>
                      
                      <div className="mt-2 pt-2 border-t border-gray-100">
                        <Link
                          href="/team"
                          className="block w-full px-3 py-2 text-center text-sm font-medium text-blue-600 hover:bg-blue-50 rounded-lg transition-colors"
                          onClick={closeAllMenus}
                        >
                          View All Team Members
                        </Link>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </div>

            {/* Desktop Social Media and Donate Button */}
            <div className="hidden lg:flex items-center gap-4">
              <div className="flex items-center gap-1">
                {socialLinks.map((social, index) => (
                  <a
                    key={social.href}
                    href={social.href}
                    target="_blank"
                    rel="noopener noreferrer"
                    aria-label={social.ariaLabel}
                    className="p-2 text-gray-500 hover:text-white hover:bg-blue-600 rounded-full transition-all duration-200 transform hover:scale-110"
                    style={{ animationDelay: `${index * 100}ms` }}
                  >
                    {social.icon}
                  </a>
                ))}
              </div>
              
              <div className="w-px h-6 bg-gray-300 mx-2"></div>
              
              <Link
                href="/get-involved"
                className="px-6 py-2.5 bg-gradient-to-r from-blue-600 to-blue-700 text-white rounded-lg font-semibold hover:from-blue-700 hover:to-blue-800 transition-all duration-200 shadow-md hover:shadow-lg transform hover:-translate-y-0.5"
                onClick={closeAllMenus}
              >
                Donate Now
              </Link>
            </div>

            {/* Mobile Menu Button */}
            <button
              onClick={() => {
                setIsOpen(!isOpen)
                if (isOpen) {
                  setExpandedMobileMenus(new Set())
                }
              }}
              className="lg:hidden p-2 hover:bg-gray-100 rounded-lg transition-colors relative"
              aria-label={isOpen ? "Close menu" : "Open menu"}
            >
              <div className="relative w-6 h-6">
                <div className={`absolute inset-0 transition-all duration-300 ${
                  isOpen 
                    ? 'opacity-0 rotate-90' 
                    : 'opacity-100 rotate-0'
                }`}>
                  <Menu size={24} className="text-gray-700" />
                </div>
                <div className={`absolute inset-0 transition-all duration-300 ${
                  isOpen 
                    ? 'opacity-100 rotate-0' 
                    : 'opacity-0 -rotate-90'
                }`}>
                  <X size={24} className="text-gray-700" />
                </div>
              </div>
            </button>
          </div>
        </div>

        {/* Mobile Navigation Overlay */}
        {isOpen && (
          <div 
            ref={mobileMenuRef}
            className="fixed inset-0 bg-black/50 z-40 lg:hidden"
            onClick={() => setIsOpen(false)}
          />
        )}

        {/* Mobile Navigation Panel */}
        <div 
          className={`lg:hidden fixed inset-y-0 right-0 w-full max-w-sm bg-white shadow-2xl z-50 transform transition-transform duration-300 ease-out ${
            isOpen ? 'translate-x-0' : 'translate-x-full'
          }`}
          onClick={(e) => e.stopPropagation()}
        >
          <div className="flex flex-col h-full overflow-y-auto">
            {/* Mobile Header */}
            <div className="flex items-center justify-between p-6 border-b border-gray-200">
              <Link 
                href="/" 
                className="flex items-center gap-3"
                onClick={closeAllMenus}
              >
                <div className="w-10 h-10 bg-gradient-to-br from-blue-600 to-blue-800 rounded-full flex items-center justify-center overflow-hidden">
                  <Image 
                    src="/images/asf-logo.png" 
                    alt="ASF Logo" 
                    width={40}
                    height={40}
                    className="w-8 h-8 object-contain"
                  />
                </div>
                <div className="flex flex-col">
                  <span className="font-bold text-gray-900">
                    Aapka Sahyog
                  </span>
                  <span className="text-xs text-gray-600">
                    Foundation
                  </span>
                </div>
              </Link>
              
              <button
                onClick={closeAllMenus}
                className="p-2 hover:bg-gray-100 rounded-full transition-colors"
                aria-label="Close menu"
              >
                <X size={20} />
              </button>
            </div>

            {/* Mobile Menu Items */}
            <div className="flex-1 px-6 py-4">
              <nav className="space-y-1">
                {links.map((link) => (
                  <Link
                    key={link.href}
                    href={link.href}
                    className="block px-4 py-3 text-base font-medium text-gray-700 hover:bg-blue-50 hover:text-blue-600 rounded-xl transition-colors nav-slide-up"
                    onClick={closeAllMenus}
                  >
                    {link.label}
                  </Link>
                ))}
                
                {/* Mobile Team Menu */}
                <div className="mt-4">
                  <div className="flex items-center justify-between px-4 py-3">
                    <span className="text-base font-medium text-gray-900">
                      Team Structure
                    </span>
                    {loading ? (
                      <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-600"></div>
                    ) : (
                      <span className="text-xs text-gray-500 bg-gray-100 px-2 py-1 rounded-full">
                        {menuHierarchy.length} sections
                      </span>
                    )}
                  </div>
                  
                  <div className="mt-2 space-y-1 max-h-[50vh] overflow-y-auto pr-2">
                    {loading ? (
                      <div className="px-4 py-8 flex items-center justify-center">
                        <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600"></div>
                      </div>
                    ) : menuHierarchy.length > 0 ? (
                      renderMobileTeamMenu(menuHierarchy)
                    ) : (
                      <div className="px-4 py-3 text-sm text-gray-500">
                        No team sections found.
                      </div>
                    )}
                  </div>
                </div>
              </nav>
            </div>

            {/* Mobile Footer */}
            <div className="p-6 border-t border-gray-200 space-y-4">
              {/* Mobile Social Links */}
              <div>
                <p className="text-sm font-medium text-gray-700 mb-3">
                  Follow Us
                </p>
                <div className="flex items-center gap-3">
                  {socialLinks.map((social) => (
                    <a
                      key={social.href}
                      href={social.href}
                      target="_blank"
                      rel="noopener noreferrer"
                      aria-label={social.ariaLabel}
                      className="p-2 text-gray-500 hover:text-white hover:bg-blue-600 rounded-full transition-all duration-200"
                      onClick={closeAllMenus}
                    >
                      {social.icon}
                    </a>
                  ))}
                </div>
              </div>

              {/* Mobile Donate Button */}
              <div className="pt-4">
                <Link
                  href="/get-involved"
                  className="block w-full px-6 py-3.5 bg-gradient-to-r from-blue-600 to-blue-700 text-white rounded-xl font-semibold text-center hover:from-blue-700 hover:to-blue-800 transition-all duration-200 shadow-md active:scale-95"
                  onClick={closeAllMenus}
                >
                  Donate Now
                </Link>
              </div>

              {/* Contact Info */}
              <div className="pt-4 border-t border-gray-100">
                <div className="text-xs text-gray-500 space-y-2">
                  <p>Email: info@aapkasahyog.org</p>
                  <p>Phone: +91 99997 67640</p>
                  <p className="mt-2">Â© {new Date().getFullYear()} Aapka Sahyog Foundation</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </nav>
    </>
  )
}